  .stars-page {
    background-size: cover;
    background-position: center;
    min-height: 100vh;
    color: white;
    padding: 2rem;
    position: relative;
  }

.sun1-image,
.sun2-image,
.sun3-image,
.sun4-image,
.sun5-image {
  position: absolute;
  width: 100px;
  height: auto;
  z-index: 10;
}

.sun-stack {
  position: absolute;
  top: 20%;
  left: 2%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.fire-font {
  font-family: 'Fire', sans-serif;
}

.fire-images {
  position: relative; /* this anchors child image positions */
  width: 100%;
  height: 100%;
}

/* First fire image - smaller and top right */
.fire-image-1 {
  position: absolute;
  top: 300px;
  right: 370px;
  width: 200px;
  height: auto;
}

